___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
   
  body {
    font-family: 'Poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
  }
  /*
    AppBar
  */
   
  .app-bar {
    padding: 8px 16px;
    background-color: #007305;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
    min-width: 100%;
  }
   
  .app-bar .app-bar__brand {
    display: flex;
    align-items: center;
  }

  .app-bar .app-bar__brand img {
    width: 50px;
    height: 50px;
    margin-right: 5px;
  }
   
  .app-bar .app-bar__brand h1 {
    color: #fbfbfb;
    text-transform: uppercase;
    font-size: 20px;
    user-select: none;
    font-weight: bold;
  }

  nav .navbar-container .nav-list {
    list-style-type: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 5px;
    position: fixed;
    background-color: #007305;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
  }

  nav .navbar-container .nav-list .nav-item img {
    width: 40px;
    height: 40px;
  }

  /*
    Main Content
  */
   
  main {
    padding: 25px;
    flex: 1;
  }
   
  .content {
    margin: 0 auto;
    min-height: 100%;
  }
   
  .content .content__heading {
    font-weight: normal;
  }

  /* Home */

  /* Status koneksi */
  .connectedStatus {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 100%;
    height: 55px;
    border: 4px solid black;
    border-radius: 10px;
  }

  .connectedStatus .connectedStatus__info {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
  }
  
  .connectedStatus .connectedStatus__buzzer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    max-width: 130px;
  }

  .connectedStatus .connectedStatus__buzzer div {
    background-color: yellow;
    border-radius: 50%;
    padding: 3px;
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease;
  }

  .connectedStatus .connectedStatus__buzzer #buzzerStatus {
    line-height: 1.1rem;
    word-wrap: break-word;
  }
  
  .connectedStatus .connectedStatus__buzzer div img {
    margin: auto;
    width: 25px;
  }

  /* Gas Value */
  /*  */
  .progress {
    width: 150px;
    height: 150px;
    margin: 20px auto;
  }
  /*  */
  .circle-progress-gas {
    width: 265px;
    height: 265px;
    border-radius: 50%;
    background-color: yellow;
    position: relative;
    margin: auto;
    margin-top: 40px;
  }

  .circle-progress-gas .circle-progress-gas__progress {
    width: 265px;
    height: 265px;
    border-radius: 50%;
    position: absolute;
    background: conic-gradient(
                yellow 0deg,
                #00ff00 180deg
            );
  }
  
  .circle-progress-gas__info {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background-color: #ffff00;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 5px solid black;
  }
  
  .text-gas-value {
    font-size: 4.4rem;
    font-weight: bold;
    color: black;
  }
  
  .text-ppm {
    font-size: 1.5rem;
    font-weight: bold;
    color: black;
    margin-top: -19px;
  }

  /* Flame Value */
  .line-progress-flame {
    width: 80%;
    height: 50px;
    margin: auto;
    margin-top: 40px;
    border-radius: 50px;
    position: relative;
  }

  .line-progress-flame__layer {
    width: 70%;
    height: 50px;
    border-radius: 50px;
  }

  .line-progress-flame .text-flame-value {
    text-align: center;
    line-height: 50px;
    font-size: 1.9rem;
    color: #fbfbfb;
    font-weight: 700;
    position: absolute;
    top: 0;
    left: -50%;
    right: -50%;
  }

  .safe-flame {
    background-color: rgb(38, 198, 38);
  }

  .safe-flame-layer {
    background-color: green;
  }

  .danger-flame {
    background-color: rgb(214, 27, 27);
  }
  
  .danger-flame-layer {
    background-color: red;
  }

  /* Status sensor */
  .statusSensor {
    margin-top: 40px;
    color: #111;
    font-weight: bold;
    font-size: 1.15rem;
  }

  .statusSensor .statusSensor_gas, .statusSensor__flame {
    margin-top: 10px;
  }

  .statusCategory {
    margin-top: 35px;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 5px;
  }

  .statusCategory .statusCategory__circle {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1.7px solid grey;
    position: relative;
  }

  .statusCategory .statusCategory__circle .statusValue{
    width: 65px;
    height: 65px;
    border-radius: 50%;
    background-color: grey;
    position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  }

  .statusCategory .statusCategory__circle .statusValue .statusValue__text {
    text-align: center;
    line-height: 65px;
    font-size: .79rem;
    font-weight: 600;
    color: #fff;
  }

  /* Animasi untuk nilai sensor < 610 */
  @keyframes blinkGreen {
    0%, 100% {
      background-color: green;
    }
    50% {
      background-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 610 */
  @keyframes blinkYellow {
    0%, 100% {
      background-color: yellow;
    }
    50% {
      background-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 800 */
  @keyframes blinkOrange {
    0%, 100% {
      background-color: orange;
    }
    50% {
      background-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 1000 */
  @keyframes blinkRed {
    0%, 100% {
      background-color: red;
    }
    50% {
      background-color: grey;
    }
  }
  @keyframes blinkBorderGreen {
    0%, 100% {
      border-color: green;
    }
    50% {
      border-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 610 */
  @keyframes blinkBorderYellow {
    0%, 100% {
      border-color: yellow;
    }
    50% {
      border-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 800 */
  @keyframes blinkBorderOrange {
    0%, 100% {
      border-color: orange;
    }
    50% {
      border-color: grey;
    }
  }

  /* Animasi untuk nilai sensor > 1000 */
  @keyframes blinkBorderRed {
    0%, 100% {
      border-color: red;
    }
    50% {
      border-color: grey;
    }
  }

  @keyframes buzzerRing {
    0%, 100% {
      transform: rotate(-15deg) scale(1.1);
    }
    
    50% {
      transform: rotate(15deg) scale(1);
    }
  }

  @keyframes blinkBackgroundBuzzerRing {
    0%, 100% {
      background-color: yellow;
    }
    50% {
      background-color: transparent;
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,sBAAsB;EACxB;;EAEA;IACE,kCAAkC;IAClC,uBAAuB;IACvB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;EACpB;EACA;;GAEC;;EAED;IACE,iBAAiB;IACjB,yBAAyB;IACzB,aAAa;IACb,oCAAoC;IACpC,SAAS;IACT,0CAA0C;IAC1C,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,iBAAiB;EACnB;;EAEA;IACE,cAAc;IACd,yBAAyB;IACzB,eAAe;IACf,iBAAiB;IACjB,iBAAiB;EACnB;;EAEA;IACE,qBAAqB;IACrB,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,QAAQ;IACR,eAAe;IACf,yBAAyB;IACzB,SAAS;IACT,OAAO;IACP,WAAW;IACX,YAAY;IACZ,4BAA4B;IAC5B,6BAA6B;EAC/B;;EAEA;IACE,WAAW;IACX,YAAY;EACd;;EAEA;;GAEC;;EAED;IACE,aAAa;IACb,OAAO;EACT;;EAEA;IACE,cAAc;IACd,gBAAgB;EAClB;;EAEA;IACE,mBAAmB;EACrB;;EAEA,SAAS;;EAET,mBAAmB;EACnB;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,aAAa;IACb,WAAW;IACX,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;EACrB;;EAEA;IACE,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,gBAAgB;EAClB;;EAEA;IACE,wBAAwB;IACxB,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,wBAAwB;EAC1B;;EAEA;IACE,mBAAmB;IACnB,qBAAqB;EACvB;;EAEA;IACE,YAAY;IACZ,WAAW;EACb;;EAEA,cAAc;EACd,KAAK;EACL;IACE,YAAY;IACZ,aAAa;IACb,iBAAiB;EACnB;EACA,KAAK;EACL;IACE,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,wBAAwB;IACxB,kBAAkB;IAClB,YAAY;IACZ,gBAAgB;EAClB;;EAEA;IACE,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,kBAAkB;IAClB;;;aAGS;EACX;;EAEA;IACE,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,yBAAyB;IACzB,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,uBAAuB;EACzB;;EAEA;IACE,iBAAiB;IACjB,iBAAiB;IACjB,YAAY;EACd;;EAEA;IACE,iBAAiB;IACjB,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;EACnB;;EAEA,gBAAgB;EAChB;IACE,UAAU;IACV,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;EACpB;;EAEA;IACE,UAAU;IACV,YAAY;IACZ,mBAAmB;EACrB;;EAEA;IACE,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,cAAc;IACd,gBAAgB;IAChB,kBAAkB;IAClB,MAAM;IACN,UAAU;IACV,WAAW;EACb;;EAEA;IACE,kCAAkC;EACpC;;EAEA;IACE,uB